
import Vue from "vue"

export default Vue.extend({
    name: "cCheckbox",
    model: {
        prop: "checked",
        event: "change",
    },
    props: {
        checked: {
            type: Boolean,
            default: false,
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        mode: {
            type: String,
            default: "primary",
            validator: (v: string) => ["primary", "red", "default"].includes(v),
        },
        nowrap: {
            type: Boolean,
            default: false,
        },
        size: {
            type: String,
            default: "m",
            validator: (v: string) => ["s", "sm", "m", "l"].includes(v),
        },
        color: {
            type: String,
        },
        hoverColor: {
            type: String,
        },
        activeColor: {
            type: String,
        },
        borderWidth: {
            type: String,
            default: "2px"
        }
    },
    computed: {
        listeners(): Record<string, Function | Function[]> {
            return {
                ...this.$listeners,
                focus: this.focus,
                blur: this.blur,
                keydown: this.keyDown,
            }
        },
        isCustom(): boolean {
            return (this.color ?? "").length > 0 || (this.hoverColor ?? "").length > 0 || (this.activeColor ?? "").length > 0
        },
    },
    methods: {
        blur(e: FocusEvent) {
            this.$emit("blur", e)
        },
        change(e: MouseEvent) {
            this.setChecked((e.target as HTMLInputElement).checked)
        },
        focus(e: FocusEvent) {
            this.$emit("focus", e)
        },
        keyDown(e: KeyboardEvent) {
            if (e.code !== "Enter" && e.code !== "Space") {
                return
            }
            this.toogle()
        },
        setChecked(v: boolean) {
            if (this.disabled) {
                return
            }
            this.$emit("change", v)
        },
        toogle() {
            this.setChecked(!this.checked)
        },
    },
})
