
import Vue from "vue"
import { arrays } from "@/utils"
import { Prop } from "vue/types/options"
import { TableColumn } from "@/../types/table"

export default Vue.extend({
    name: "cTable",
    props: {
        isFullWidth: {
            type: Boolean,
            default: false,
        },
        title: {
            type: String,
            default: "",
        },
        titleSize: {
            type: String,
            default: "14px",
        },
        titlePosition: {
            type: String,
            default: "center",
            validator: (v: string) => ["left", "center", "right", "end", "start", "unset"].includes(v),
        },
        columns: {
            type: Array as Prop<TableColumn[]>,
            default: (): TableColumn[] => [],
        },
        items: {
            type: Array as Prop<any[]>,
            default: (): any[] => [],
        },
        needSorted: {
            type: Boolean,
            default: false,
        },
        align: {
            type: String,
            default: "left",
            validator: (v: string) => ["left", "center", "right"].includes(v),
        },
        borderRadius: {
            type: String,
            default: "0px",
        },
        headerBackgroundColor: {
            type: String,
            default: "#4e62d1",
        },
        headerColor: {
            type: String,
            default: "#ffffff",
        },
        sortedArrowColor: {
            type: String,
            default: "#ffffff",
        },
        rowBackgroundColor: {
            type: String,
            default: "#3f51b511",
        },
        rowColor: {
            type: String,
            default: "#1e1e1e",
        },
        isSelectingRow: {
            type: Boolean,
            default: false,
        },
        selectingRowDataTooltip: {
            type: String,
        },
        selectingRowTooltip: {
            type: String,
        },
        selectingRowHoverBackground: {
            type: String,
            default: "#5167e231",
        },
        width: {
            type: String,
            default: "fit-content",
        },
        maxWidth: {
            type: String,
            default: "none",
        },
    },
    data() {
        const sortOrders = {} as { [key: string]: number }
        this.columns.forEach((c) => {
            sortOrders[c.key] = 1
        })
        return {
            sortKey: this.columns[0].key,
            sortOrders,
            hasCustomSlot: !!this.$slots.custom || !!this.$scopedSlots.custom,
        }
    },
    computed: {
        hasCaption(): boolean {
            return (this.title ?? "").length > 0
        },
        filteredColumns(): TableColumn[] {
            return this.columns.filter((c) => c.key !== "custom")
        },
        customColumn(): TableColumn {
            return this.columns.find((c) => c.key === "custom") ?? ({} as TableColumn)
        },
        preparedItems(): any[] {
            const order = this.sortOrders[this.sortKey] ?? 1
            return this.needSorted
                ? arrays.clone(this.items).sort((a, b) => {
                    a = parseInt(a[this.sortKey]) !== NaN ? parseInt(a[this.sortKey]) : a[this.sortKey]
                    b = parseInt(b[this.sortKey]) !== NaN ? parseInt(b[this.sortKey]) : b[this.sortKey]

                    return (a === b ? 0 : a > b ? 1 : -1) * order
                })
                : this.items
        },
    },
    mounted() {
        this.checkSlots()
    },
    updated() {
        this.checkSlots()
    },
    methods: {
        clicked(event: string, item: any) {
            this.$emit(event, item)
        },
        checkSlots() {
            this.hasCustomSlot = !!this.$slots.custom || !!this.$scopedSlots.custom
        },
        sortBy(key: string) {
            if (this.needSorted) {
                this.sortKey = key
                this.sortOrders[key] = this.sortOrders[key] * -1
            }
        },
        selectRow(item: any) {
            if (this.isSelectingRow) {
                this.$emit("select", item)
            }
        },
    },
})
