
import Vue from "vue"

export default Vue.extend({
    name: "cAlert",
    props: {
        closeable: {
            type: Boolean,
            default: false,
        },
        hideIcon: {
            type: Boolean,
            default: false,
        },
        status: {
            type: String,
            validator: (v: string) => ["info", "success", "warning", "error"].includes(v),
        },
        backgroundColor: {
            type: String,
        },
        titleColor: {
            type: String,
        },
        titleSize: {
            type: String,
        },
        descColor: {
            type: String,
        },
        descSize: {
            type: String,
        },
        iconColor: {
            type: String,
        },
        iconCloseColor: {
            type: String,
        },
        iconCloseHoverColor: {
            type: String,
        },
        iconCloseSize: {
            type: String,
        },
        borderRadius: {
            type: String,
        },
        iconPositionCenter: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            isClosing: false,
            show: false,
            showDesc: false,
            showTitle: false,
        }
    },
    computed: {
        hasCustomBackgroundColor(): boolean {
            return (this.backgroundColor ?? "").length > 0
        },
        hasCustomIconColor(): boolean {
            return (this.iconColor ?? "").length > 0
        },
        hasCustomTitleColor(): boolean {
            return (this.titleColor ?? "").length > 0
        },
        hasCustomDescColor(): boolean {
            return (this.descColor ?? "").length > 0
        },
        hasCustomTitleSize(): boolean {
            return (this.titleSize ?? "").length > 0
        },
        hasCustomDescSize(): boolean {
            return (this.descSize ?? "").length > 0
        },
    },
    mounted() {
        this.checkSlots()
    },
    updated() {
        this.checkSlots()
    },
    methods: {
        checkSlots() {
            this.showTitle = !!this.$slots.title
            this.showDesc = !!this.$slots.desc
            this.show = this.showTitle || this.showDesc
        },
        close() {
            this.isClosing = true
            setTimeout(() => {
                this.show = false
                this.$emit("close")
            }, 350)
        },
    },
})
