
import Vue from "vue"

export default Vue.extend({
    name: "cToggle",
    model: {
        prop: "isEnabled",
        event: "toggle",
    },
    props: {
        isEnabled: {
            type: Boolean,
            default: false,
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        size: {
            type: String,
            default: "l",
            validator: (v: string) => ["m", "l"].includes(v),
        },
        color: {
            type: String,
            default: "#3f51b5",
        },
        customSize: {
            type: String,
        },
    },
    computed: {
        height(): string {
            return this.customSize !== undefined ? this.customSize : this.size === "m" ? "15px" : "20px"
        },
    },
    methods: {
        toggle() {
            if (!this.disabled) {
                this.$emit("toggle", !this.isEnabled)
            }
        },
    },
})
