
import Vue from "vue"

export default Vue.extend({
    name: "cRadio",
    props: {
        value: {
            type: String,
            default: null,
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        checked: {
            type: Boolean,
            default: false,
        },
        nowrap: {
            type: Boolean,
            default: false,
        },
        mode: {
            type: String,
            default: "primary",
            validator: (v: string) => ["primary", "red"].includes(v),
        },
        color: {
            type: String,
        },
        hoverColor: {
            type: String,
        },
        activeColor: {
            type: String,
        },
    },
    computed: {
        listeners(): Record<string, Function | Function[]> {
            return {
                ...this.$listeners,
                focus: this.focus,
                blur: this.blur,
                keydown: this.keyDown,
                change: this.change,
            }
        },
        isCustom(): boolean {
            return (this.color ?? "").length > 0 || (this.hoverColor ?? "").length > 0 || (this.activeColor ?? "").length > 0
        },
    },
    methods: {
        change() {
            this.setChecked()
            this.$emit("change", this.value)
        },
        focus(e: FocusEvent): void {
            this.$emit("focus", e)
        },
        blur(e: FocusEvent): void {
            this.$emit("blur", e)
        },
        keyDown(e: KeyboardEvent): void {
            if (e.code !== "Enter" && e.code !== "Space") {
                return
            }
            e.preventDefault()
            this.toogle()
        },
        toogle() {
            this.setChecked()
        },
        setChecked() {
            if (this.disabled) {
                return
            }
            this.$emit("update:checked", this.value)
        },
    },
})
