
import Vue from "vue"
import { ButtonCustomStyle } from "@/../types/button"
import { Prop } from "vue/types/options"

export default Vue.extend({
    name: "cButton",
    props: {
        mode: {
            type: String,
            default: "normal",
            validator: (v: string) =>
                [
                    "normal",
                    "orange",
                    "primary",
                    "gradient",
                    "crimson",
                    "disabled",
                    "white-to-crimson",
                    "red",
                    "green",
                    "white",
                    "just-text",
                ].includes(v),
        },
        type: {
            type: String,
            default: "button",
            validator: (v: string) => ["button", "submit", "reset"].includes(v),
        },
        isLoading: {
            type: Boolean,
            default: false,
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        size: {
            type: String,
            default: "m",
            validator: (v: string) => ["s", "m", "l", "xl"].includes(v),
        },
        customStyle: {
            type: Object as Prop<ButtonCustomStyle>,
        },
        dontUpperCase: {
            type: Boolean,
            default: false,
        },
        isMobile: {
            type: Boolean,
            default: false,
        },
        width: {
            type: String,
        },
        height: {
            type: String,
        },
    },
    data() {
        return {
            isTouch: false,
        }
    },
    computed: {
        listeners(): Record<string, Function | Function[]> {
            return {
                ...this.$listeners,
                click: this.click,
            }
        },
    },
    methods: {
        click(e: MouseEvent) {
            if (this.isMobile) {
                setTimeout(
                    () => {
                        this.$emit("click", e)
                    },
                    this.mode === "gradient" ? 300 : 100,
                )
            } else {
                this.$emit("click", e)
            }
        },
        touchStart() {
            this.isTouch = true
        },
        touchEnd() {
            setTimeout(
                () => {
                    this.isTouch = false
                },
                this.mode === "gradient" ? 300 : 100,
            )
        },
        mouseDown() {
            this.isTouch = true
        },
        mouseUp() {
            setTimeout(
                () => {
                    this.isTouch = false
                },
                this.mode === "gradient" ? 300 : 100,
            )
        },
        touchMove(e: TouchEvent) {
            const btn = this.$refs.btn as HTMLElement
            const isAbove = e.changedTouches[0].pageY < btn.offsetTop
            const isBelow = e.changedTouches[0].pageY > btn.offsetTop + btn.offsetHeight
            const isLeft = e.changedTouches[0].pageX < btn.offsetLeft
            const isRight = e.changedTouches[0].pageX > btn.offsetLeft + btn.offsetWidth

            if (isAbove || isBelow || isLeft || isRight) {
                this.isTouch = false
            }
        },
    },
})
