
import Vue from "vue"
import cEmoji from "@/components/Emoji.vue"

export default Vue.extend({
    name: "cEdit",
    components: { cEmoji },
    model: {
        prop: "value",
        event: "update:value",
    },
    props: {
        cols: {
            type: Number,
            default: 42,
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        readonly: {
            type: Boolean,
            default: false,
        },
        isClear: {
            type: Boolean,
            default: false,
        },
        isConfirmationCode: {
            type: Boolean,
            default: false,
        },
        isError: {
            type: Boolean,
            default: false,
        },
        isLazy: {
            type: Boolean,
            default: false,
        },
        isPassword: {
            type: Boolean,
            default: false,
        },
        isShowIcon: {
            type: Boolean,
            default: false,
        },
        isValid: {
            type: Boolean,
            default: false,
        },
        isWarning: {
            type: Boolean,
            default: false,
        },
        mode: {
            type: String,
            default: "singleline",
            validator: (v: string) => ["singleline", "multiline"].includes(v),
        },
        placeholder: {
            type: String,
        },
        required: {
            type: Boolean,
            default: false,
        },
        resize: {
            type: String,
            default: "vertical",
            validator: (v: string) => ["vertical", "horizontal", "none", "both"].includes(v),
        },
        rows: {
            type: Number,
            default: 4,
        },
        type: {
            type: String,
            default: "text",
            validator: (v: string) => ["text", "number", "password", "email", "tel"].includes(v),
        },
        value: {
            type: String,
            default: "",
        },
        codeLength: {
            type: Number,
            default: 6,
        },
        inputmode: {
            type: String,
            default: "text",
        },
        isHideStatus: {
            type: Boolean,
            default: false,
        },
        isSearch: {
            type: Boolean,
            default: false,
        },
        isSearchLeft: {
            type: Boolean,
            default: false,
        },
        isSearchIconBold: {
            type: Boolean,
            default: true,
        },
        iconName: {
            type: String,
        },
        iconColor: {
            type: String,
            default: "#3F51B5",
        },
        autocomplete: {
            type: String,
            default: "off",
        },
        isHideRequired: {
            type: Boolean,
            default: false,
        },
        enableEmoji: {
            type: Boolean,
            default: false,
        },
        emojiPosition: {
            type: String,
            default: "",
            validator: (v: string) =>
                ["", "top-center", "top-left", "top-right", "bottom-center", "bottom-left", "bottom-right", "left", "right"].includes(v),
        },
        emojiIconColor: {
            type: String,
            default: "#3f51b5",
        },
        emojiIconHoverColor: {
            type: String,
            default: "#4960df",
        },
        width: {
            type: String,
            default: "360px",
        },
        backgroundColor: {
            type: String,
            default: "#ebf0f9",
        },
        placeholderColor: {
            type: String,
            default: "#818ca9",
        },
        color: {
            type: String,
            default: "#1e1e1e",
        },
        borderActiveColor: {
            type: String,
            default: "#3f51b5",
        },
        borderWidth: {
            type: String,
            default: "1px",
        },
        clearIconColor: {
            type: String,
            default: "#818ca9",
        },
        clearIconHoverColor: {
            type: String,
            default: "#3f51b5",
        },
        mask: {
            type: String,
        },
        maxlength: {
            type: String,
        },
    },
    data() {
        return {
            text: "",
            isShowPassword: false,
            isFocus: false,
            hasStatusText: false,
            formattedPhone: "",
            formattedMask: "",
            formattedCode: "",
            code: Object.fromEntries(Array.from({ length: this.codeLength }, (_, i) => [i + 1, ""])) as { [key: number]: string },
        }
    },
    computed: {
        iconTitle(): string {
            return this.isShowPassword ? "Скрыть пароль" : "Показать пароль"
        },
        isMultiline(): boolean {
            return this.mode === "multiline"
        },
        isPhone(): boolean {
            return this.type === "tel"
        },
        passwordMode(): string {
            return this.isShowPassword ? "text" : "password"
        },
        listeners(): Record<string, Function | Function[]> {
            return {
                ...this.$listeners,
                input: this.input,
                change: this.change,
                focus: this.focus,
                blur: this.blur,
                keydown: this.keyDown,
            }
        },
        multilineBinds(): any {
            const style = {
                "--backgroundColor": this.backgroundColor,
                "--color": this.color,
                "--borderActiveColor": this.borderActiveColor,
                "--borderWidth": this.borderWidth,
                resize: ` ${this.readonly ? "none" : this.resize}`,
            } as { [key: string]: string }

            if (this.isMultiline && this.cols === 42) {
                style["--width"] = this.width
            }
            const binds = [
                { required: this.required },
                { disabled: this.disabled },
                { readonly: this.readonly },
                { rows: this.rows },
                { cols: this.cols },
                {
                    class: {
                        error: this.isError,
                        warning: this.isWarning,
                        readonly: this.readonly,
                        "no-resize": this.resize === "none" || this.readonly,
                        "custom-width": this.isMultiline && this.cols === 42,
                    },
                },
                { autocomplete: this.autocomplete },
                { ref: "input" },
                { inputmode: this.inputmode },
                { style: style },
            ] as any

            if (this.maxlength !== undefined) {
                binds.push({ maxlength: this.maxlength })
            }

            if (!this.isLazy) {
                binds.push({ value: this.value })
            }

            return binds
        },
        singlelineBinds(): any {
            const binds = [
                { type: this.isPassword ? this.passwordMode : this.type },
                { required: this.required },
                { disabled: this.disabled },
                { readonly: this.readonly },
                {
                    class: {
                        code: this.isConfirmationCode,
                        phone: this.isPhone,
                        password: this.isPassword,
                        error: this.isError,
                        warning: this.isWarning,
                        readonly: this.readonly,
                        entered: this.value.length > 0 || this.formattedPhone.length > 0 || this.formattedMask.length > 0,
                    },
                },
                { autocomplete: this.autocomplete },
                { ref: "input" },
                { inputmode: this.inputmode },
                {
                    style: {
                        "--width": this.width,
                        "--backgroundColor": this.backgroundColor,
                        "--color": this.color,
                        "--borderActiveColor": this.borderActiveColor,
                        "--borderWidth": this.borderWidth,
                    },
                },
            ] as any

            if (this.maxlength !== undefined) {
                binds.push({ maxlength: this.maxlength })
            }

            if (!this.isPhone) {
                binds.push({ value: this.value })
            }

            return binds
        },
        maskNumbers(): number[] {
            return (this.mask ?? "").split(/\D/).reduce((acc, cur) => {
                if (/\d/g.test(cur)) {
                    acc.push(Number(cur))
                }
                return acc
            }, [] as number[])
        },
        maskSeparators(): string[] {
            return (this.mask ?? "").split("").reduce((acc, cur) => {
                if (/\D/g.test(cur)) {
                    acc.push(cur)
                }
                return acc
            }, [] as string[])
        },
    },
    watch: {
        value: {
            immediate: true,
            handler() {
                this.text = this.value
                if (this.isPhone) {
                    if ((this.value || "").length !== 0) {
                        this.phoneFormatting("value")
                    }
                } else if (this.mask !== undefined) {
                    if ((this.value || "").length !== 0) {
                        this.maskFormatting("value")
                    }
                }
            },
        },
    },
    mounted() {
        this.checkSlots()
    },
    updated() {
        this.checkSlots()
    },
    methods: {
        selectEmoji(emoji: any) {
            const textarea = this.isMultiline ? (this.$refs.textarea as HTMLTextAreaElement) : (this.$refs.input as HTMLInputElement)
            const start = textarea.selectionStart as number
            const end = textarea.selectionEnd as number
            if (this.isLazy) {
                this.text = `${this.text.slice(0, start)}${emoji}${this.text.slice(end)}`
            } else {
                this.$emit("update:value", `${this.value.slice(0, start)}${emoji}${this.value.slice(end)}`)
            }
            textarea.setRangeText(emoji, start, end, "end")
        },
        blur(e: FocusEvent) {
            this.isFocus = false
            this.$emit("blur", e)
        },
        change(e: Event) {
            if (this.isLazy) {
                this.$emit("update:value", this.text)
            }
            this.$emit("change", e)
        },
        checkSlots() {
            this.hasStatusText = !!this.$slots["status-text"]
        },
        clear() {
            this.$emit("update:value", "")
            if (this.isPhone) {
                this.formattedPhone = ""
            } else if (this.mask !== undefined) {
                this.formattedMask = ""
            }
            this.$emit("input", "")
            this.isFocus = false
            setTimeout(() => {
                ;(this.$refs.input as HTMLInputElement).blur()
            }, 100)
        },
        focus(e: FocusEvent) {
            this.isFocus = true
            this.$emit("focus", e)
        },
        input(e: InputEvent) {
            if (this.isPhone) {
                if (e.inputType === "deleteContentBackward" && this.formattedPhone.length < 1) {
                    this.$emit("update:value", "")
                    return
                }

                this.phoneFormatting("formattedPhone")
                this.$emit("input", e)

                return
            }
            if (!this.isPhone && this.mask !== undefined) {
                if (e.inputType === "deleteContentBackward" && this.formattedMask.length < 1) {
                    this.$emit("update:value", "")
                    return
                }

                this.maskFormatting("formattedMask")
                this.$emit("input", e)

                return
            }

            if (this.isConfirmationCode) {
                const index = Number((e.target as HTMLInputElement).dataset.id)
                this.code[index] = this.code[index].replace(/\D/g, "")
                const refs = this.$refs["ui-edit"] as HTMLInputElement

                if (this.code[index].length > 0 && index !== Object.keys(this.code).length) {
                    const next = refs.querySelector(`input[data-id="${index + 1}"]`) as HTMLElement
                    next.focus()
                }

                const result = Object.values(this.code).reduce((a, b) => a + b)
                this.$emit("update:value", result)

                return
            }

            if (!this.isLazy) {
                this.$emit("update:value", (e.target as HTMLInputElement | HTMLTextAreaElement).value)
            }
            this.$emit("input", e)
        },
        keyDown(e: KeyboardEvent) {
            if (this.isConfirmationCode) {
                const index = Number((e.target as HTMLInputElement).dataset.id)
                const refs = this.$refs["ui-edit"] as HTMLInputElement
                const prev = refs.querySelector(`input[data-id="${index - 1}"]`) as HTMLElement
                const next = refs.querySelector(`input[data-id="${index + 1}"]`) as HTMLElement

                if (["Backspace", "Delete"].includes(e.code)) {
                    if (this.code[index].length > 0) {
                        this.code[index] = ""
                        setTimeout(() => {
                            if (index !== 1) {
                                prev.focus()
                            }
                        }, 1)
                    } else {
                        if (index !== 1) {
                            this.code[index - 1] = ""

                            prev.focus()
                        }
                    }

                    const result = Object.values(this.code).reduce((a, b) => a + b)
                    this.$emit("update:value", result)
                }

                if (e.code === "ArrowRight" && index !== Object.keys(this.code).length) {
                    next.focus()
                }

                if (e.code === "ArrowLeft" && index !== 1) {
                    prev.focus()
                }
            }
            this.$emit("keydown", e)
        },
        phoneFormatting(phone: keyof { formattedPhone: string; value: string }) {
            let p = this[phone].replace(/\D/g, "")

            if (p.length > 10 && ["7", "8"].includes(p[0])) {
                p = p.slice(1)
            }
            const match = this.maskNumbers.reduce((acc, cur) => (acc += `${`(\\d{0,${cur}})`}`), "")

            const m = match.length > 0 ? p.match(new RegExp(match)) : p.match(/(\d{0,3})(\d{0,3})(\d{0,2})(\d{0,2})/)

            if (m === null) {
                return
            }

            let res = ""
            if (match.length > 0) {
                for (let i = 1; i < m.length; i++) {
                    res +=
                        m[i] +
                        (m[i].length === Number(this.maskNumbers[i - 1])
                            ? this.maskSeparators[i - 1] !== undefined && m[i + 1]
                                ? this.maskSeparators[i - 1]
                                : ""
                            : "")
                }
            }

            this.formattedPhone = res || (!m[2] ? m[1] : m[1] + " " + m[2] + (m[3] ? "-" + m[3] : "") + (m[4] ? "-" + m[4] : ""))
            this.$emit("update:value", m[0])
        },
        maskFormatting(text: keyof { formattedMask: string; value: string }) {
            let val = this[text].replace(/\D/g, "")
            const match = this.maskNumbers.reduce((acc, cur) => (acc += `${`(\\d{0,${cur}})`}`), "")
            const m = val.match(new RegExp(match))

            if (m === null) {
                return
            }

            let res = ""
            for (let i = 1; i < m.length; i++) {
                res +=
                    m[i] +
                    (m[i].length === Number(this.maskNumbers[i - 1])
                        ? this.maskSeparators[i - 1] !== undefined && m[i + 1]
                            ? this.maskSeparators[i - 1]
                            : ""
                        : "")
            }
            this.formattedMask = res
            this.$emit("update:value", m[0])
        },
        showPassword() {
            this.isShowPassword = !this.isShowPassword
        },
    },
})
