
import Vue from "vue"

export default Vue.extend({
    name: "cArrowSVG",
    props: {
        color: {
            type: String,
            default: "#818ca9",
        },
        hoverColor: {
            type: String,
            default: "#818ca9",
        },
        size: {
            type: String,
            default: "m",
            validator: (v: string) => ["s", "m", "l"].includes(v),
        },
        mode: {
            type: String,
            default: "normal",
            validator: (v: string) => ["normal", "circle"].includes(v),
        },
        circleColor: {
            type: String,
            default: "#ffffff",
        },
        isUp: {
            type: Boolean,
            default: false,
        },
        isDown: {
            type: Boolean,
            default: false,
        },
        isLeft: {
            type: Boolean,
            default: false,
        },
        isNoShadow: {
            type: Boolean,
            default: false,
        },
        rotate: {
            type: String,
            default: undefined,
        },
    },
    computed: {
        listeners(): Record<string, Function | Function[]> {
            return {
                ...this.$listeners,
                click: this.click,
            }
        },
    },
    methods: {
        click(e: MouseEvent) {
            this.$emit("click", e)
        },
    },
})
